






























































































































































































































































































.wrapper{
	position: relative;
}

.app-container{
	width: 70%;
	height: calc(100vh - 50px);
	overflow-y: scroll;
	padding: 10px;
}

.guide-container{
	width: 30%;
	background: #ddd;
	height: 100%;
	min-height: calc(100vh - 100px);
	position: absolute;
	top: 0;
	right: 0;
	padding: 10px;
	overflow-y: scroll;
	border-left: 2px solid #aaa
}

.item-section{
	background: #bbb;
	padding-bottom: 1px;
	margin-bottom: 10px;
	border-radius: 3px;
}

.item-heading{
	padding: 10px 0;
	padding-left: 15px;
}

.item-heading:hover{
	cursor: pointer;

}

.guide-item{
	background: white;
	padding: 15px;
	font-size: 15px;
	margin: 15px;
	margin-top: 0;
	border-radius: 3px;
}

.guide-item:last-child{
}

.guide-item pre{
	font-size: 14px;
	overflow-x: scroll;
	margin-bottom: 15px;
}

.guide-item p{
	margin-bottom: 15px;
}

.guide-item code{
	word-break: break-all;
}

.guide-item a{
	color: blue;
	word-break: break-all;
}

.guide-item a:hover{
	text-decoration: none;
}

.file-line{
	color: gray;
	font-style: italic;
}




